@media only screen and (max-width: 768px) {
    .index {
        width: 18%;
        text-align: center;
        font-size: 22px;
        margin-bottom: 5vh;
        margin-left: auto;
        margin-right: auto;

        padding-top: 6px;
        padding-bottom: 8px;

        border-left: 4px solid black;
        border-right: 4px solid black;
        border-radius: 50%;
        font-weight: lighter;
    }

    .project-preview-container {
        margin-top: 1.5vh;
        width: 95vw;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
        color: black;
        text-decoration: none;
    }

    .project-cover-image-container {
        position: relative;
    }

    .project-cover-video {
        width: 95vw;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
        display: inline-block;
        z-index: 20;
    }

    .project-cover-video-hidden {
        visibility: hidden;
    }

    .project-cover-image {
        width: 95vw;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
        display: inline-block;
        position: absolute;
    }

    .project-cover-image-hidden {
        visibility: hidden;
    }

    .project-info-container {
        margin-top: 3vh;
    }

    .project-title {
        margin-bottom: 3vh;
        display: inline-block;
        font-size: 14px;
        letter-spacing: -0.5px;
    }

    .hovering-project-title {
        text-decoration: underline;
    }

    .project-metadata {
        display: flex;
    }

    .project-metadata-title {
        margin-bottom: .2vh;
        font-style: italic;
        width: 25%
    }

    .project-metadata-value {
        width: 75%;
        text-align: right;
        ;
    }

    .project-info-container {
        display: block;
    }

    .project-info-row1 {
        width: 95vw;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
    }

    .project-info-row2 {
        margin-top: 2vh;
        width: 95vw;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
        margin-bottom: 3.5vh;
    }
}

@media only screen and (min-width: 769px) {
    .right-most-preview-container {
        margin-left: 1.5vw;
    }

    .project-preview-container {
        margin-right: 1.5vw;
        color: black;
        text-decoration: none;
        width: calc(80vh * 1.5) !important;
    }

    .project-cover-image-container {
        position: relative;
    }

    .project-cover-video {
        height: 80vh;
        display: inline-block;
        z-index: 20;
    }

    .project-cover-video-hidden {
        visibility: hidden;
    }

    .project-cover-image {
        height: 80vh;
        display: inline-block;
        position: absolute;
    }

    .project-cover-image-hidden {
        visibility: hidden;
    }

    .project-info-container {
        margin-top: 2vh;
        margin-bottom: 2vh;
        display: flex;
    }

    .project-title {
        margin-bottom: 1.5vh;
        display: inline-block;
    }

    .hovering-project-title {
        text-decoration: underline;
    }

    .project-metadata-container {
        display: flex;
    }

    .project-metadata {
        margin-right: 6%;
    }

    .project-metadata-title {
        margin-bottom: .2vh;
        font-style: italic;
    }

    .project-info-row1 {
        width: 50%;
    }

    .project-info-row2 {
        width: 50%;
        float: right;
    }
}